<template>
  <el-container class="ard">
    <Header title="文章详情"/>
    <el-main class="ard-main cm-vertical cm-flex">
      <h3>{{article.title}}</h3>
      <!--标题底部信息栏-->
      <aside class="cm-horizon ard-info cm-padding-v">
        <el-link type="primary" @click="$linkTo(`/ArticleList/${article.type}`)">{{articleType}}</el-link>
        <span class="cm-padding-h">{{dateFormat(article.updatedAt)}}</span>
        <el-link type="primary" @click="$linkTo(`/ArticleAddOrEdit/${article.type}/${article.objectId}`)" v-if="admin">
          编辑
        </el-link>
        <el-link type="primary" class="cm-padding-h" @click="deleteArticle(article.objectId)" v-if="admin">
          删除
        </el-link>
      </aside>
      <!--主文章内容-->
      <article class="ard-content cm-font-sm cm-vertical cm-full-w cm-overflow-y">
        <pre ref="content" class="cm-flex" v-html="article.content"></pre>
        <el-drawer
          :show-close="false"
          :visible.sync="catalogWindow"
          :modal-append-to-body="false"
          direction="ltr">

        </el-drawer>
      </article>
      <!--文章底部信息栏-->
      <aside class="cm-horizon cm-full-w ard-count-info cm-border-b cm-padding-v">
        <!--<ul class="cm-horizon">-->
        <!--<li class="ard-image">-->
        <!--<el-image :src="imageList.length>0?imageList[0].url:''"></el-image>-->
        <!--</li>-->
        <!--</ul>-->
        <a @click="showCatalog" class="ard-catalog">目录</a>
        <div>
          <el-link>评论:10</el-link>
          <el-link class="cm-padding-h">点赞:30</el-link>
        </div>
      </aside>
      <!--评论列表-->
      <article class="cm-full-w cm-font-xs">
        <section class="cm-horizon ard-comment-cont" v-for="(item,index) in commentList" :key="index">
          <aside class="cm-vertical cm-padding-h">
            <el-avatar :src="item.avatar"/>
            <span class=" cm-color-green">{{item.username}}</span>
          </aside>
          <main class="cm-flex cm-align-l cm-border-b">
            <div>{{item.content}}</div>
            <div class="cm-align-c cm-full-w cm-bg-grey ard-more" @click="showReply(item.objectId)">
              <el-link type="primary">查看回复</el-link>
            </div>
            <footer class="cm-horizon ard-comment-info">
              <span class="cm-flex">{{timeFormat(item.createdAt)}}</span>
              <div class="cm-font-md">
                <i class="el-icon-delete" v-if="currentUserId===item.userObjectId"
                   @click="deleteComment(item.objectId,1)"></i>
                <i class="el-icon-chat-dot-square cm-padding-h" @click="showCommentDialog(item.userObjectId)"></i>
                <i class="el-icon-star-off"></i>
              </div>
            </footer>
          </main>
        </section>
      </article>
    </el-main>
    <!--页面底部操作按钮-->
    <el-footer>
      <el-button-group class="cm-full-w cm-horizon">
        <el-button class="cm-flex" type="primary" @click="showCommentDialog()"><i class="el-icon-chat-dot-square"></i>
          评论
        </el-button>
        <el-button class="cm-flex" type="primary"><i class="el-icon-star-off"></i> 赞</el-button>
      </el-button-group>
    </el-footer>
    <!--评论回复对话框-->
    <aside class="ard-mask cm-vertical cm-full cm-center" v-if="showComment">
      <main class="ard-mask-main cm-bg-white">
        <header class="cm-align-l cm-padding">评论</header>
        <el-input type="textarea" v-model="comment" resize="none" class="cm-radius-n cm-padding" :rows="4"></el-input>
        <el-footer>
          <el-button-group class="cm-full-w cm-horizon">
            <el-button class="cm-flex cm-radius-n" @click="cancelCommentDialog">取消</el-button>
            <el-button class="cm-flex cm-radius-n" @click="submitCommentDialog">提交</el-button>
          </el-button-group>
        </el-footer>
      </main>
    </aside>
    <!--回复列表框-->
    <el-drawer
      :visible.sync="showReplyDialog"
      size="70%"
      title="回复列表"
      :show-close="false"
      direction="btt">
      <section class="cm-horizon cm-font-xs ard-comment-cont cm-full-w cm-padding-h" v-for="(item,index) in replyList"
               :key="index">
        <aside class="cm-vertical cm-padding-h">
          <el-avatar :src="item.avatar"/>
          <span class=" cm-color-green">{{item.username}} 回复 {{item.toUserName}}</span>
        </aside>
        <main class="cm-flex cm-align-l cm-border-b">
          <div class="ard-comment-content">{{item.content}}</div>
          <footer class="cm-horizon ard-comment-info">
            <span class="cm-flex">{{timeFormat(item.createdAt)}}</span>
            <div class="cm-font-md">
              <i class="el-icon-delete" v-if="currentUserId===item.userObjectId"
                 @click="deleteComment(item.objectId,2)"></i>
              <i class="el-icon-chat-dot-square cm-padding-h" @click="showCommentDialog(item.userObjectId)"></i>
              <i class="el-icon-star-off"></i>
            </div>
          </footer>
        </main>
      </section>
    </el-drawer>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
  </el-container>
</template>

<script>
  import {AV, getArticleFileList} from '../../common/api';
  import {formatDate} from '../../common/utils';
  import Model from '../../model/';
  export default {
    components: {}, computed: {
      articleType(){
        if (!this.article.type) {
          return '';
        }
        return Model['articleType'][this.article.type].name;
      }
    },
    watch: {
      imageList: {
        handler(v, ov){
          this.showArticleFile();
        }
      }
    },
    methods: {
      getArticleDetail(){
        const {objectId} = this.$route.params;
        const loading = this.$loading(this.$loadingConfig)
        AV.getData('articleList', objectId).then(res => {
          this.article = res;
          this.content = res.content;
          getArticleFileList(res, this);
          return res;
        }).then((res) => {
          this.getArticles(this.showWordLink);
        }).catch(e => {
          console.error(e);
          this.$alert('获取失败,' + e);
        }).finally(() => {
          loading.close()
        })
      },
      isAdmin(){
        AV.isAdmin().then((is) => {
          this.admin = is;
        })
      },
      dateFormat(date){
        return formatDate(date, false);
      },
      timeFormat(date){
        return formatDate(date, true);
      },
      getCommentList(){
        const query = new AV.Query('comment');
        query.equalTo('articleId', this.articleId);
        query.descending('createdAt');
        query.include(['userId']);
        query.find().then(res => {
          this.commentList = [];
          this.formatCommentData(res, 'comment');
        });
      },
      getReplyList(commentId){
        const query = new AV.Query('comment');
        query.equalTo('commentId', commentId);
        query.descending('createdAt');
        query.include(['userId', 'toUserId']);
        query.find().then(res => {
          this.replyList = [];
          this.formatCommentData(res, 'reply');
        }).catch(e => {
          this.$alert('error:' + e);
        });
      },
      showCommentDialog(replyId = null){
        this.showComment = true;
        if (replyId) {
          this.dialogType = 2;
          this.dialogId = replyId;
        } else {
          this.dialogId = this.articleId
          this.dialogType = 1;
        }
      },
      cancelCommentDialog(){
        this.comment = '';
        this.showComment = false;
      },
      submitCommentDialog(){
        if (!this.comment) {
          this.$message({message: '内容不可为空！', type: 'warning'});
          return;
        }
        const handler = new AV.Object('comment');
        const userObject = AV.User.current();
        handler.set('userId', userObject);
        handler.set('content', this.comment);
        if (this.dialogType === 1) {
          handler.set('articleId', this.dialogId);
        } else {
          const toUser = AV.Object.createWithoutData('_User', this.dialogId);
          handler.set('toUserId', toUser);
          handler.set('commentId', this.commentId);
        }
        handler.save().then(() => {
          if (this.dialogType === 1) {
            this.getCommentList();
          } else {
            this.getReplyList(this.commentId);
          }
        }).catch(e => {
          this.$message('提交失败,' + e);
        }).finally(() => {
          this.cancelCommentDialog();
        });
      },
      showReply(commentId){
        this.showReplyDialog = true;
        this.commentId = commentId;
        this.getReplyList(commentId);
      },
      formatCommentData(res, type){
        const result = AV.decodeData(res);
        for (let item of result) {
          const {content, userId, toUserId, createdAt, objectId} = item;
          let comment = {};
          comment.content = content;
          comment.createdAt = createdAt;
          const {avatar, username} = userId;
          comment.avatar = avatar ? avatar.url : '';
          comment.username = username || '';
          comment.objectId = objectId;
          comment.userObjectId = userId.objectId;

          comment.toUserName = toUserId ? toUserId.username : '';
          if (type === 'comment') {
            this.commentList.push(comment);
          } else {
            this.replyList.push(comment);
          }
        }
      },
      deleteComment(objectId, type){
        AV.deleteData('comment', objectId).then(res => {
          if (type === 1) {
            this.getCommentList();
          } else {
            this.getReplyList(this.commentId);
          }
        }).catch(e => {
          this.$message('删除失败，' + e);
        })
      },
      getArticles(callback=null){
        return AV.queryData('articleList', [{operator: '<', data: [{'type': 4}]}, {
          operator: '==',
          data: [{'group': 1}]
        }]).then(res => {
          this.articles = res;
          callback&&callback();
        }).catch(e => {
          this.$message('API 报错:' + e);
        })
      },
      showWordLink(){
        if (this.articles.length>0 && this.content) {
          const articles = this.articles;
          const content = this.content;
          let resultContent = content;

          for (let article of articles) {
            const {title, objectId} = article;
            const start = content.indexOf(article.title);
            if (start >= 0) {
              const reg = new RegExp(`${title}`, 'g');
              let baseName=this.$config.common.baseName;
              resultContent = resultContent.replace(reg, `<a href='${baseName}#/ArticleDetail/${objectId}'>` + title + "</a>")
            }
          }
          this.article.content = resultContent;
          this.$forceUpdate();
        }
      },
      showArticleFile(){
        if (this.content) {
          let content = this.content;
          const reg = /\[(image+\-[0-9])\]/g;
          let matchArr = content.match(reg);
          const fileList = AV.decodeData(this.imageList);
          if (matchArr && matchArr.length > 0) {
            for (let match of matchArr) {
              const matchStr = match;
              let index = content.indexOf(matchStr);
              const start = index + 7;
              const end = content.indexOf(']');
              let imageIndex = content.substring(start, end)
              if (imageIndex < 1 || imageIndex > 3) {
                return;
              }
              imageIndex = Number(imageIndex - 1);
              const src = fileList[imageIndex] ? fileList[imageIndex].url : '';
              if (src) {
                content = content.replace(matchStr, `<div class="cm-horizon cm-center cm-padding-v"><img src='${src}' class="ard-content-image" /></div>`);
              }
            }
          }
          this.article.content = content;
          const dom = this.$refs.content.childNodes;
        }
      }, handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      showCatalog(){
          this.catalogWindow=true;
      },
      deleteArticle(objectId){
        this.$confirm('确认删除?').then(() => {
          const loading = this.$loading(this.$loadingConfig)
          AV.deleteData('articleList', objectId).then(res => {
            this.$linkTo('/');
          }).catch(e => {
            this.$alert($t('CommonApiError'));
            console.error('error of Home module', e)
          }).finally(() => {
            loading.close()
          })
        }).catch(e => {

        })
      },
    },
    data(){
      const {objectId} = this.$route.params;
      const currentUser = AV.decodeData(AV.User.current());
      return {
        article: {},
        content: '',
        articles: [],
        admin: false,
        comment: '',
        commentId: '',
        commentList: [],
        imageList: [],
        replyList: [],
        showComment: false,
        articleId: objectId,
        showReplyDialog: false,
        currentUserId: currentUser.objectId,
        dialogId: null,
        dialogType: 1,//1:article comment 2:reply to comment
        dialogImageUrl: '',
        dialogVisible: false,
        catalogWindow:false,
      }
    },
    created(){

    },
    mounted(){
      this.getArticleDetail();
      this.isAdmin();
      this.getCommentList();
    },
  }
</script>

<style>
  .ard {
  }

  .ard-info {
    width: 100%;
    font-size: var(--font-sm);
    color: grey;
    align-items: center;
  }

  .ard-content {
    min-height: 200px;
    padding: 10px;
    text-align: left;
    background-color: #ebeef5;
    position: relative;
    border: var(--border);
  }

  .ard-content > pre {
    font-family: Constantia, "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    white-space: pre-line;
    margin: 0;
    line-height: 1;
  }

  .ard-item-cont{
    display: flex;
    flex-wrap: wrap;
  }
  .ard-item-cont>li{
    flex-basis: 50%;
    margin-bottom: var(--size-xs);
  }
  .ard-item-cont label{
    font-size: 14px;
    font-weight: bold;
    color:var(--cor-red);
    margin-right: var(--size-xs);
  }
  .ard-item-cont label:after{
    content:':';
  }


  .ard-content p {
    text-indent: 20px;
    margin:5px 0;
  }

  .ard-content .v-modal{
    /*position: absolute;*/
  }

  .ard-catalog{
    border: var(--border);
    background-color: var(--cor-grey);
  }

  .ard-main {
    align-items: flex-start;
    width: 100%;
  }

  .ard-count-info {
    justify-content: space-between;
  }

  .ard-comment-info {
    color: #929292;
    padding-bottom: 5px;
  }

  .ard-comment-cont {
    margin: 10px 0;
  }

  .ard-more {
    padding: 5px 0;
    margin: 5px 0;
  }

  .ard-mask {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 2500;
    background-color: rgba(0, 0, 0, 0.7);
  }

  .ard-mask-main {
    width: 70%;
    min-width: 200px;
    max-width: 300px;
  }

  .ard-comment-content {
    min-height: 30px;
  }
  /*.ard .el-drawer__wrapper{*/
    /*position: absolute;*/
  /*}*/
  .ard .el-drawer__header {
    text-align: center;
    margin-bottom: 0 !important;
    padding: 10px !important;
    border-bottom: var(--border);
  }

  .ard-content .el-drawer__header {
    border-bottom: 0;
  }

  .ard-image {
    width: 70px;
    height: 70px;
    margin-right: 5px;
  }

  .ard-image > .el-image {
    width: 100%;
    height: 100%;
  }

  .ard-image img {
    width: 100%;
    height: 100%;

  }

  .ard-content-image {
    width: 50%;
    height: 50%;
  }
</style>



