<template>
  <el-pagination
    class="cm-padding-v cm-horizon cm-center"
    :page-sizes="[3,10, 20, 30, 40]"
    layout="prev,pager,next,sizes"
    @current-change="queryList"
    @prev-click="queryList"
    @next-click="queryList"
    @size-change="changePageSize"
    :total="totalCount">
  </el-pagination>
</template>

<script>
  import {AV} from '../../common/api';
  export default {
    props: {
      table: {type: String, required: true},
      query: {type: Array, required: true},
      callback: {type: Function}
    },
    computed: {},
    watch: {
      query(newVal, oldVal){
        this.queryList();
      }
    },
    methods: {
      queryList(currentPage = 1){
        let condition = AV.decodeData(this.query);
        this.currentPage = currentPage;
        let skip = (this.currentPage - 1) * this.pageSize;
        condition.push({operator: 'limit', data: [{count: this.pageSize}]});
        condition.push({operator: 'skip', data: [{count: skip}]});
        AV.queryData(this.table, condition).then(res => {
          this.$emit('getPageList', res);
          return res;
        }).then((res) => {
          if (this.callback) {
            this.callback(res);
          }
        });
        AV.queryData(this.table, condition, true).then(res => {
          this.totalCount = res;
        });
      },
      changePageSize(pageSize){
        this.pageSize = pageSize;
        this.queryList();
      }
    },
    data(){
      return {
        pageSize: this.$config.common.pageSize,
        currentPage: 1,
        totalCount: 0,
      }
    },
    created(){

    },
    mounted(){
      this.queryList();
    },
  }
</script>

<style>
</style>



