<template>
  <el-container class="arl">
    <Header title="" class="cm-header">
      <div class="arl-input">
        <el-input placeholder="输入文章标题搜索" v-model="keyword" @input="search(keyword)">
          <el-button slot="append" @click="search(keyword)" icon="el-icon-search"></el-button>
        </el-input>
      </div>
    </Header>

    <el-main class="arl-search-result">
      <h3 class="arl-title">{{title}}</h3>
      <ul ref="ul">
        <li class="arl-product" v-for="(result, index) in results" :key="index"
            @click="$linkTo(`/ArticleDetail/${result.objectId}`)">

          <el-card>
            <main class="arl-product-main">
              <article>
                <section class="cm-horizon">
                  <aside>
                    <img class="arl-icon" :src="result.icon?result.icon:defaultIcon"/>
                  </aside>
                  <section class="cm-font-xs">
                    <h4>{{result.title}}</h4>
                    <div class="cm-color-green">{{getArticleContent(result.content)}}</div>
                  </section>
                </section>
                <footer class="arl-operate-cont">
                  <el-button-group v-if="admin">
                    <el-button type="warning" icon="el-icon-document" size="small"
                               @click.stop="$linkTo(`/ArticleAddOrEdit/${articleType}/${result.objectId}`)">{{$t('Edit')}}
                    </el-button>
                    <el-button type="danger" icon="el-icon-document" size="small"
                               @click.stop="deleteArticle(result.objectId)">{{$t('Delete')}}
                    </el-button>
                  </el-button-group>
                </footer>
              </article>
            </main>
          </el-card>
        </li>
      </ul>
    </el-main>
    <footer>
      <Pages table="articleList" :query="query" @getPageList="getPageList" :callback="getArticleIcon"/>
      <el-button v-if="admin" class="arl-footer-btn" @click="$linkTo(`/ArticleAddOrEdit/${articleType}`)"
                 type="success">写文章
      </el-button>
    </footer>
  </el-container>
</template>

<script>
  import {removeHtml} from '../../common/utils'
  import {AV} from '../../common/api';
  import defaultIcon from '../../assets/logo.png';
  import Model from '../../model';
  export default {
    components: {},
    computed: {
      title(){
        return Model[Number(this.articleType)];
      },
      getIcon(){
        this.results.forEach((item) => {
          if (!item.icon) {
            item.icon = defaultIcon;
          }
        });
      }
    },
    methods: {
      search (keyword) {
          console.log('key',keyword);
        this.keyword = keyword
        this.getList();
      },
      getList(){
        const articleType = Number(this.articleType);
        let condition = [
          {operator: '==', data: [{type: articleType}, {group: 1}]},
          {operator: 'order', data: [{desc: 'updatedAt'}]}
        ];
        if (this.admin) {
          condition = [
            {operator: '<=', data: [{group: 2}]},
            {operator: '==', data: [{type: articleType}]},
            {operator: 'order', data: [{desc: 'updatedAt'}]}
          ];
        }
        if (this.keyword) {
          condition.push({operator: 'like', data: [{title: this.keyword}]});
        }
        this.query = condition;
      },
      getPageList(data){
        this.results = data;
      },
      isAdmin(){
        return AV.isAdmin().then((is) => {
          this.admin = is;
        })
      },
      deleteArticle(objectId){
        this.$confirm('确认删除?').then(() => {
          const loading = this.$loading(this.$loadingConfig)
          AV.deleteData('articleList', objectId).then(res => {
            this.getList();
          }).catch(e => {
            this.$alert($t('CommonApiError'));
            console.error('error of Home module', e)
          }).finally(() => {
            loading.close()
          })
        }).catch(e => {

        })
      },
      getArticleContent(content){
        content = removeHtml(content);
        if (content.length > 50) {
          content = content.substr(0, 50);
        }
        return content;
      },
      getArticleIcon(articleList){
        articleList.forEach((item) => {
          if (item.fileList && item.fileList.length > 0) {
            AV.getData('_File', item.fileList[0]).then((res) => {
              const {url} = res;
              item.icon = url;
              this.results = articleList;
              this.$forceUpdate();
            }).catch(e => {
              this.$msg('获取图片失败:' + e);
            });
          }
        });
      }
    },
    data () {
      return {
        keyword: '',
        admin: false,
        defaultIcon,
        articleType: this.$route.params.type,
        results: [],
        query: [],
      }
    },
    created () {
    },
    mounted () {
      this.isAdmin().then(() => {
        this.getList();
      });
    }
  }
</script>

<style>
  .arl-input {
    flex: 1;
    width: auto;
    margin-left: var(--size-md);
  }

  .arl-title {
    text-align: left;
    padding-left: var(--size-sm);
  }

  .arl-icon-cont {
    width: 60px;
    height: 60px;
  }

  .arl h3 {
    margin-bottom: var(--size-sm);
  }

  .arl .el-main {
    flex: 1;
    overflow-y: scroll;
    padding: 0;
  }

  .arl-input .el-input__inner {
    height: 30px;
    line-height: 30px;
  }

  .arl .hd-title {
    flex: 0;
    margin-right: 0;
  }

  .arl-input .el-button {
    padding: 5px;
  }

  .arl-search-result {
    margin-top: var(--size-sm);
  }

  .arl-icon {
    width: 60px;
    height: 60px;
    border: var(--border);
    padding: var(--size-xs);
    margin-right: var(--size-sm);
  }

  .arl-product {
    margin-bottom: var(--size-sm);
  }

  .arl .el-card__body {
    display: flex;
    padding: var(--size-sm);
  }

  .arl-keywords {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: var(--size-sm);
    border-bottom: var(--border);
  }

  .arl-product-main {
    flex: 1;
    text-align: left;
    font-size: 14px;
  }

  .arl-tag-cont {
    padding: var(--size-sm) 0 0 0;
  }

  .arl-results-label {
    text-align: left;
    padding: var(--size-sm);
  }

  .arl-desc {
    color: var(--cor-green)
  }

  .arl-operate-cont {
    height: 32px;
    display: flex;
    justify-content: flex-end;
    border-top: var(--border);
    padding: var(--size-xs);
    padding-bottom: 0;
  }

  .arl-footer-btn {
    width: 100%;
    border-radius: 0 !important;
  }
</style>



