<template>
  <el-row :gutter="10">
    <el-col :xs="24" :span="8" class="ho-card-cont" v-for="(article, index) in articles" :key="index" :offset="0">
      <el-card class="ho-card" shadow="never"
               :body-style="{ padding: '5px'}">
        <div class="art-type" :class="'cm-bg-'+articleTypes[article.type].className"></div>
        <img :src="article.icon?article.icon:defaultIcon" class="ho-image"
             @click="$linkTo(`/ArticleDetail/${article.objectId}`)"/>
        <div class="ho-info-cont">
          <main class="ho-title" @click="$linkTo(`/ArticleDetail/${article.objectId}`)">
            <h5>{{article.title}}</h5>
            <span
              class="cm-font-xs cm-color-green">{{getArticleContent(article.content)}}</span>
          </main>
          <footer>
            <el-button-group class="operate-button-cont">
              <el-button size="small" type="primary" icon="el-icon-chat-dot-square">{{$t('评论')}} 10</el-button>
              <el-button size="small" type="primary" plain icon="el-icon-star-off">{{$t('点赞')}} 30</el-button>
            </el-button-group>
          </footer>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
  import {removeHtml} from '../../common/utils';
  import defaultIcon from '../../assets/logo.png';
  import Model from '../../model';
  export default {
    props: ['articles'],
    computed: {},
    methods: {
      getArticleContent(content){
        if (content) {
          content = removeHtml(content);
          if (content.length > 30) {
            content = content.substr(0, 30) + '...';
          }
        }
        return content;
      }
    },
    data () {
      return {
        defaultIcon,
        articleTypes: Model['articleType'],
      }
    },
    created () {

    },
    mounted () {

    }
  }
</script>

<style>
  .ho-title {
    flex: 1;
    text-align: left;
  }

  .ho-title h5{
    margin-top: 0;
    margin-bottom: 0;
  }

  .ho-image {
    align-self: center;
    width: 80px;
    height: 80px;
    display: block;
    object-fit: cover;
    border: var(--border);
    background-color: var(--cor-white);
  }

  .ho-info-cont {
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-left: var(--size-xs);
    color:var(--cor-black)
    /*background-color: #f7f7f7;*/
  }

  .art-type {
    height: 100%;
    width: 3px;
    margin-right: var(--size-sm);
  }

  .ho-card-cont {
    margin-bottom: var(--size-sm);
  }
  .ho-card{
    height: 93px;
  }
  .ho-card > .el-card__body {
    display: flex !important;
    align-items: stretch !important;
    height: 100%;
  }
</style>



