<template>
  <el-container class="ho">
    <Header :showBack="false" title="首页">
      <div @click="linkTo('/search')">
        <i class="el-icon-search ho-search"></i>
        <span class="hidden-xs-only ho-search-text">{{$t('Search')}}</span>
      </div>
    </Header>
    <main class="ho-main">
      <el-carousel class="ho-hot-cont" indicator-position="outside" :autoplay="true" trigger="click" height="150px">
        <el-carousel-item v-for="(item,index) in noticeList" :key="index">
          <div class="ho-notice cm-bg-grey cm-full" @click="$linkTo(`/ArticleDetail/${item.objectId}`)">
            <img v-if="item.icon" :src="item.icon?item.icon:''" class="ho-hot-img"/>
            <div class="ho-notice-text cm-vertical">{{item.content}}</div>
          </div>
        </el-carousel-item>
      </el-carousel>
      <el-tabs tab-position="left" v-model="currentCate" @tab-click="changeTab">
        <el-tab-pane name="0">
          <span slot="label">最新</span>
          <Article :articles="articleList"/>
        </el-tab-pane>
        <el-tab-pane v-for="(item,index) in articleTypes" :label="item.name" :name="String(Number(index))"
                     :key="Number(index)">
          <span slot="label"> {{item.name}}</span>
          <Article :articles="articleList"/>
        </el-tab-pane>
      </el-tabs>
      <el-footer v-if="admin">
        <el-button type="success" @click="$linkTo('/ArticleAddOrEdit/1')">写文章</el-button>
      </el-footer>
    </main>
  </el-container>
</template>

<script>
  import config from '../../../config/index'

  import Article from './article.vue'
  import {saveLoginUser, getCacheData, saveCacheData} from '../../common/utils';
  import {AV, getArticleFileList} from '../../common/api';
  import Model from '../../model';
  export default {
    components: {
      Article
    },
    computed: {},
    methods: {
      isAdmin(callback){
        AV.isAdmin().then((is) => {
          this.admin = is;
        }).then(() => {
          callback && callback();
        })
      },
      linkTo (path) {
        this.$router.push(path)
      },
      changeTab () {
        this.getList(this.currentCate)
      },
      getList(type){
        const articleType = Number(type);
        this.articleList = [];
        if (articleType === 0) {
          this.list.forEach(item => {
            if (item.type <= 4) {
              this.articleList.push(item);
            }
          })
        } else {
          this.list.forEach(item => {
            if (item.type === articleType) {
              this.articleList.push(item);
            }
          })
        }
      },
      getArticleList(){
        const tableName = 'articleList';
        const loading = this.$loading(this.$loadingConfig)
        let condition = [
          {operator: '==', data: [{group: 1}]},
          {operator: '<=', data: [{type: 4}]},
          {operator: 'limit', data: [{count: 20}]},
          {operator: 'order', data: [{desc: 'updatedAt'}]}
        ];
        if (this.admin) {
          //type<=4,所有类型文章，不显示其他类目
          condition = [
            {operator: '<=', data: [{group: 2}, {type: 4}]},
            {operator: 'limit', data: [{count: 20}]},
            {operator: 'order', data: [{desc: 'updatedAt'}]}
          ];
        }
        AV.queryData(tableName, condition).then((res) => {
          return res;
        }).then((res) => {
          res.forEach(item => {
            const fileList = item.fileList;
            if (fileList && fileList.length > 0) {
              const icon = fileList[0];
              AV.getData('_File', icon).then((result) => {
                const {url} = result;
                item.icon = url;
                this.list.push(item);
                this.getList(0);
              }).catch(e => {
                this.$msg('获取图片失败:' + e);
              });
            } else {
              this.list.push(item);
              this.getList(0);
            }
          })

        }).catch(e => {
          console.error('error of home module', e)
        }).finally(() => {
          loading.close()
        });
      }, getNoticeList(){
        const tableName = 'articleList';
        let condition = [
          {operator: '==', data: [{group: 1}, {type: 5}]},
          {operator: 'limit', data: [{count: 3}]},
          {operator: 'order', data: [{desc: 'updatedAt'}]}
        ];
        AV.queryData(tableName, condition).then((res) => {
          res.forEach(item => {
            const fileList = item.fileList;
            if (fileList && fileList.length > 0) {
              const icon = fileList[0];
              AV.getData('_File', icon).then((result) => {
                const {url} = result;
                item.icon = url;
                this.noticeList.push(item);
              }).catch(e => {
                this.$msg('获取图片失败:' + e);
              });
            } else {
              this.noticeList.push(item);
            }
          })
        }).catch(e => {
          console.error('error of home module', e)
        });
      }
    },
    data () {
      const articleTypes = AV.decodeData(Model['articleType']);
      delete articleTypes[5];
      delete articleTypes[99];
      return {
        isCollapse: true,
        currentDate: new Date(),
        list: [],//total article list
        articleList: [],//current cate article list
        noticeList: [],//notice list
        imageList: [],
        currentCate: '0',
        articleTypes,
        admin: false,
      }
    },
    created () {
    },
    mounted () {
      this.isAdmin(this.getArticleList.bind(this));
      this.getNoticeList();
    }
  }
</script>

<style>
  .ho {
    width: 100%;
  }

  .ho-hot-cont {
    /*margin-bottom: var(--size-md);*/
  }

  .ho-notice {
    position: relative;
  }

  .ho-notice-text {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    min-height: 30px;
    background-color: white;
    opacity: 0.7;
    font-size: var(--font-sm);
    justify-content: flex-end;
  }

  .ho-hot-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .ho-search {
  }

  .ho-search-text {
    font-size: var(--font-sm);
    color: #303133;
  }

  .ho-main {
    display: flex;
    flex-direction: column;
    flex: 1;
    width: 100%;
    overflow: hidden;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: white;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: white;
  }

  .operate-button-cont {
    display: flex !important;
    width: 100%;
  }

  .operate-button-cont > .el-button {
    flex: 1;
    padding: 5px;
  }

  .ho .el-tabs {
    flex: 1;
  }

  .ho .el-tabs__item {
    padding: 0 var(--size-sm) 0 0;
  }

  .ho .el-tabs__content, .ho .el-tab-pane, .ho .el-row {
    height: 100%;
    /*background-color: var(--cor-grey);*/
  }

  .ho .el-row {
    overflow-y: scroll;
  }
</style>



