<template>
  <div class="login-main">
    <div class="login-form-cont">
      <form class="login-form">
        <div class="login-form-item">
          <label class="login-label" for="username">User Name</label><input type="text" id="username" name="username"/>
        </div>
        <div class="login-form-item">
          <label  class="login-label" for="password">Password</label><input type="password" id="password" name="username"/>
        </div>
        <div class="login-form-item">
          <button>Login</button>
          <button>Exit</button>
        </div>
      </form>
    </div>
    <div class="login-bottom">
      <div class="login-bottom-info">
        Copyright © 2021 SequisLife All rights reserved.

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    computed: {},
    methods: {},
    data(){
      return {}
    },
    created(){

    },
    mounted(){

    },
  }
</script>

<style>
  .login-main {
    position: relative;
    width: 789px;
    height: 606px;
    margin: 10px auto;
    background-image: url(bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
  }

  .login-form-cont {
    position: absolute;
    bottom: 64px;
    right: 0;
    height: 210px;
    width: 400px;
    background-color: #fff;
    font-size: 14px;
    text-align: center;
  }

  .login-form{
    margin-top: 70px;
  }

  .login-label{
    display: inline-block;
    text-align: left;
    width: 80px;
  }

  .login-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 64px;
    width: 100%;
    background-color: #ebebeb;
  }

  .login-form-item{
    margin: 10px;
  }

  .login-bottom-info {
    height: 24px;
    width: 100%;
    margin-top: 20px;
    line-height: 24px;
    background-color: #cdcdcd;
    font-size: 12px;
  }
</style>



