import config from '../../config';
const AV = require('leancloud-storage');

AV.init({
  appId: config.common.leanAppId,
  appKey: config.common.leanAppKey,
  region: config.common.leanRegion
});

//condition:[{operator:'<>',data:[{type:1},{type:3}]},{operator:'order',data:[{'asc':articleOrder},{'desc':updateAt}]}]
export function queryData(tableName, condition = null, getCount = false) {
  let query = new AV.Query(tableName);
  if (condition) {
    for (let item of condition) {
      for (let d of item.data) {
        for (let key in d) {
          if (d.hasOwnProperty(key)) {
            switch (item.operator) {
              case '==': {
                query.equalTo(key, d[key]);
              }
                break;
              case '<>': {
                query.notEqualTo(key, d[key]);
              }
                break;
              case '>': {
                query.greaterThan(key, d[key]);
              }
                break;
              case '<': {
                query.lessThan(key, d[key]);
              }
                break;
              case '<=': {
                query.lessThanOrEqualTo(key, d[key]);
              }
                break;
              case '>=': {
                query.greaterThanOrEqualTo(key, d[key]);
              }
                break;
              case 'order': {
                if (key === 'asc') {
                  query.addAscending(d[key]);
                } else {
                  query.addDescending(d[key]);
                }
              }
                break;
              case 'limit': {
                query.limit(d[key]);
              }
                break;
              case 'include': {
                query.include(d[key]);
              }
                break;
              case 'like': {
                query.contains(key, d[key]);
              }
                break;
              case 'skip': {
                query.skip(d[key]);
              }
                break;
            }
          }
        }
      }
    }
  }
  if (!getCount) {
    return query.find().then((data) => {
      return JSON.parse(JSON.stringify(data));
    }).catch((e) => {
      this.$msg('error when get data:' + e);
    });
  } else {
    return query.count().then((data) => {
      return JSON.parse(JSON.stringify(data));
    }).catch((e) => {
      this.$msg('error when get data:' + e);
    });
  }
}

export function getData(tableName, objectId, relatedTable = null) {
  const query = new AV.Query(tableName);
  return query.get(objectId).then((res) => {
    return decodeData(res)
  }).catch((e) => {
    console.error(e)
  });
}

export function countData(tableName) {
  const query = new AV.Query(tableName);
  return query.get(objectId).then((res) => {
    return decodeData(res)
  }).catch((e) => {
    console.error(e)
  });
}

export function addData(tableName, data, callback = null) {
  const handler = new AV.Object(tableName);
  for (let key in data) {
    if (data.hasOwnProperty(key)) {
      handler.set(key, data[key]);
    }
  }
  return handler.save().then((res) => {
    if (res) {
      const result = JSON.parse(JSON.stringify(res));
      callback && callback(result);
      return result;
    } else {
      throw new Error("data error");
    }
  }).catch((e) => {
    console.error(e)
  });
}

export function updateData(updateData, relatedData = null) {
  let relatedObject;
  if (relatedData) {
    relatedObject = AV.Object.createWithoutData(relatedData.tableName, relatedData.objectId);
  }
  const handler = AV.Object.createWithoutData(updateData.tableName, updateData.objectId);

  for (let fieldName in updateData.data) {
    if (updateData.data.hasOwnProperty(fieldName)) {
      const value = updateData.data[fieldName];
      if (value === '[RELATED]') {
        handler.set(fieldName, relatedObject);
      } else {
        handler.set(fieldName, value);
      }
    }
  }
  return handler.save().then(res => {
    return decodeData(res);
  });
}

export function deleteData(tableName, objectId) {
  const handler = AV.Object.createWithoutData(tableName, objectId);
  return handler.destroy();
}
export function decodeData(AVResponse) {
  return JSON.parse(JSON.stringify(AVResponse));
}
export function isAdmin() {
  return AV.User.current().getRoles().then((roles) => {
    const rolesArr = AV.decodeData(roles)
    let isAdmin = false;
    for (let role of rolesArr) {
      if (role.name === 'admin') {
        isAdmin = true;
        break;
      }
    }
    return isAdmin;
  });
}
export function getArticleFileList(res, _this) {
  if (!res.fileList) {
    return;
  }
  let index = -1;
  for (let item of res.fileList) {
    index++;
    let currentIndex = index;
    AV.getData('_File', item).then((result) => {
      const {name, url, objectId} = result;
      _this.imageList.push({name, url, objectId, currentIndex});
      _this.imageList.sort((a, b) => {
        return a.currentIndex < b.currentIndex
      });
    }).catch(e => {
      this.$msg('获取图片失败:' + e);
    });
  }
}
AV.addData = addData;
AV.queryData = queryData;
AV.getData = getData;
AV.updateData = updateData;
AV.deleteData = deleteData;
AV.decodeData = decodeData;
AV.isAdmin = isAdmin;
export {AV};



